@keyframes dash {
  0% {
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
    fill: transparent;
  }
  90% {
    stroke-dashoffset: 0;
    fill: transparent;
  }
  100% {
    stroke-dashoffset: 0;
    fill: #000000;
  }
}

.p-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 120;
  transform: translateY(0%);
  overflow: hidden;
  opacity: 1;
  pointer-events: all;
  background-color: #EEEEEA;
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-loading > div {
  white-space: nowrap;
  letter-spacing: 0.15em;
}

.p-loading > div svg {
  width: 180px;
  height: 101px;
}

@media screen and (max-width: 800px) {
  .p-loading > div svg {
    width: 140px;
    height: 42px;
  }
}

.p-loading > div svg path {
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  fill: transparent;
  stroke: #000000;
}

body.is-load-end .p-loading {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.75s 2.75s cubic-bezier(0.08, 0.92, 0.35, 1);
}

body.is-load-end .p-loading > div {
  transform: translateY(-80px);
  transition: transform 1s 2.3s ease-in;
}

body.is-load-end .p-loading > div svg path {
  animation: dash 2.5s ease-in forwards;
}

